.works {
  /*grid-area: w;*/
  /*grid-gap: 10px;*/
  /*margin: 10px 10% 10px 10%;*/
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
}
.container {
  background-color: rgb(35%, 35%, 35%);
  min-height: 80vh;
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: lightgreen;
  border: 1px solid;
}

.wrap {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  /*margin: 10px;*/
  background-color: darkgray;
  border: 1px solid;
  min-height: 70vh;
  width: 100%;
  flex-wrap: wrap;
}

.block {
  display: flex;
  flex-direction: column;
  min-height: 600px;
  width: 500px;
  /*font-size: 12px;*/
  align-items: center;
  justify-content: space-between;
  text-align: center;
  /*flex-wrap: wrap;*/
  /*background-color: rgb(20%, 20%, 20%);*/
  /*color: cyan;*/
  border: 1px solid;
}

.cube {
  display: flex;
  height: 150px;
  width: 100%;
  flex-direction: column;
  background-color: lightgray;
  color: white;
  justify-content: space-evenly;
  font-weight: bold;
}

.img {
  height: 300px;
  width: 300px;
  text-align: center;
  /*border-radius: 50%;*/
  /*background-color: dimgrey;*/
  color: white;
  justify-content: center;
  align-items: center;
  /*order: 1;*/
  align-self: center;
  margin: auto;
}

